body {
  margin: 0;
}

.HomeLayout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
  grid-auto-rows: minmax(10px, auto);
}

.Logo {
  grid-column: 1 / 1;
  grid-row: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 65px;
}

.Line {
  grid-column: 1 / 6;
  grid-row: 2 / 2;
  height: 30px;
  width: auto;
  background-color: #79b829;
}

.Image {
  cursor: pointer;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 0px 0px 0px;
}

.Themes {
  grid-column: 1;
  grid-row: 3 / 5;
  text-align: center;
  width: 100%;
}

.College {
  cursor: pointer;
  display: inline-block;
  vertical-align: top;
  border: 5px solid transparent;
  position: relative;
}

.College-image {
  vertical-align: middle;
  height: 120px;
  width: 200px;
  border: 1px solid #d0d8d0;
  position: relative;
}

.College-text {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: bold;
  border-radius: 0px 0px 2px 2px;
  border: transparent 0px;
  height: 30px;
  width: 150px;
}
.video-overlay-play-button {
  box-sizing: border-box;
  height: 120px;
  width: 200px;
  padding: 10px calc(50% - 40px);
  position: absolute;
  top: 1px;
  left: 0;
  display: block;
  opacity: 0.3;
  cursor: pointer;
  background-image: linear-gradient(transparent, #000);
  transition: opacity 150ms;
}

.VideoPlayer {
  width: 100%;
  height: 95%;
  position: relative;
  background: black;
  text-align: center;
  border-radius: 5px 5px 5px 5px;
}

.Video {
  height: 100%;
}

.Video-content {
  padding: 15px;
  width: 90%;
  height: 90%;
}

.Title {
  position: absolute;
  top: 20px;
  left: 20px;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 10px 20px;
}

.Title h2 {
  margin: 0;
  font-size: 16px;
  font-weight: normal;
  font-family: Arial, Helvetica, sans-serif;
}

.Colleges {
  grid-column: 2/ 5;
  grid-row: 3/5;
  width: auto;
  height: 440px;
  border: rgb(218, 222, 224) 1px solid;
  border-radius: 5px 5px 5px 5px;
  padding: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
  display: inline-block;
  background-color: #fbfbfb;
  margin: 10px 0px 0px 0px;
}

.Footer {
  grid-column: 1 / 6;
  grid-row: 6;
  height: 100%;
  width: 100%;
  background-color: rgb(236, 235, 235);
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 11px;
}

.VideoMediaPlayer {
  max-width: 850px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 50px;
  background: white;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.VideoMediaPlayer-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -15px;
  top: -15px;
  border-radius: 50%;
  border: none;
  background: #171c30;
  color: white;
  cursor: pointer;
}

.VideoMediaPlayer-close:after {
  content: 'X';
  font-weight: bold;
  font-size: 30px;
}

