.Colleges {
  grid-column: 2/ 5;
  grid-row: 3/5;
  width: auto;
  height: 440px;
  border: rgb(218, 222, 224) 1px solid;
  border-radius: 5px 5px 5px 5px;
  padding: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
  display: inline-block;
  background-color: #fbfbfb;
  margin: 10px 0px 0px 0px;
}
